.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 25px;
    left: 89%;
    top: 17px;
  }

  .menu-item {
      color: white;
      font-size: 30px;
      font-family: Lora;
      line-height: 0.2rem;
      letter-spacing: 1px;
      text-decoration: none;
  }

  .homemade__title-sidebar {
      font-family: Pepe;
      color: white;
  }

  .menu-item:hover {
      color: #98a900;
      text-decoration: none;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: black;
    padding: 3.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
    margin-bottom: 15px;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

  .sidebar__align-config {
      display: flex;
  }

  .sidebar__food-container {
      display: flex;
  }

  .side__bar-food-config {
      width: 100px;
      margin-bottom: 5px;
      margin-left: 5px;
  }


  @media only screen and (min-width: 200px) {
    @media only screen and (max-width: 499px) {
      .bm-burger-button {
        position: fixed;
        width: 36px;
        height: 20px;
        left: 85%;
        top: 15px;
      }
     
    }
  }